import moment from 'moment'

import momentLocale from './moment.cn'
import { isNotNilOrEmpty } from './ramda-extra'

const FORMAT_DATETIME_MEDIUM = 'YYYY-MM-DD HH:mm'
const LOCALE = 'zh-cn'

const setupLocale = () => {
  if (moment.locales().indexOf(LOCALE) !== -1) {
    moment.locale(LOCALE, null) // clear existing locale
  }

  moment.locale('zh-cn', momentLocale)
  moment.locale('en') // reset active locale
}

const formatTime = (time, format = FORMAT_DATETIME_MEDIUM) =>
  isNotNilOrEmpty(time) ? moment(time).locale('zh-cn').format(format) : ''  // eslint-disable-line prettier/prettier

export { FORMAT_DATETIME_MEDIUM, formatTime, setupLocale }
